import ErmjBaseView from "./ErmjBaseView";
import { ErmjAudioConst } from "../data/ErmjPathHelper";
import ErmjGameConst from "../data/ErmjGameConst";

export default class ErmjAskBtnView extends ErmjBaseView {

    private autoBtn: cc.Node;
    private cancelAuto: cc.Node;
    private autoCloseBtn: cc.Node;

    constructor(node: cc.Node) {
        super();
        this.setNode(node);
    }

    protected initView() {
        this.autoBtn = this.getChild('autoBtn');
        this.autoBtn.active = false;
        this.autoCloseBtn = this.getChild('autoBtn/autoClose');
        this.autoCloseBtn.active = false;
        ErmjGameConst.addCommonClick(this.autoBtn, "", this.onAutoPlayClick, this);
        this.cancelAuto = this.getChild('cancelAuto');
        this.cancelAuto.active = false;
        ErmjGameConst.addCommonClick(this.node, "cancelAuto/btnCancel", this.onCancelAutoClick, this);
    }


    protected onClose() {
        this.clearByRound();
    }

    public clearByRound() {
        this.autoBtn.active = false;
        this.cancelAuto.active = false;
        this.autoCloseBtn.active = false;
    }

    public clearByGame() {
        this.autoBtn.active = false;
        this.cancelAuto.active = false;
        this.autoCloseBtn.active = false;
    }

    //挂机按钮
    private onAutoPlayClick() {
        ErmjGameConst.playSound(ErmjAudioConst.commonAudio.ButtonClick, true);
        this.setAutoPlayBtnShow(false);
        Game.Server.send(this.Define.CmdAuto, { auto_play: 1 });
    }

    //取消挂机按钮
    private onCancelAutoClick() {
        ErmjGameConst.playSound(ErmjAudioConst.commonAudio.ButtonClick, true);
        this.setAutoPlayBtnShow(true);
        Game.Server.send(this.Define.CmdAuto, { auto_play: 0 });
    }

    public setAutoPlayBtnShow(isShow: boolean) {
        this.autoBtn.active = true;
        this.cancelAuto.active = !isShow;
        this.autoCloseBtn.active = !isShow;
    }
}